/**
 * One-character webfont for cJ logo – kept separate so it can load in <head>
 *
 * @author Chris Barnes http://www.cjbarnes.co.uk
 * @version 1.0.0
 */

@font-face {
    font-family: 'cJ Logo';
    src: url('/font/cj-logo.eot');
    }

@font-face {
    font-family: 'cJ Logo';
    src: url(data:application/x-font-woff;charset=utf-8;base64,) format('woff'),
         url('/font/cj-logo.ttf') format('truetype'),
         url('/font/cj-logo.svg#icomoonregular') format('svg');
    font-weight: bold;
    font-style: normal;
}